.todoList {
    display: flex;
    height: 100%;
    font-size: 16px;
    text-align: left;
    > div:first-child {
        flex: 1;
        height: 100%;
    }
}
.hoverOpen {
    display: flex;
    flex-direction: column;
    .bookMark,
    .footprint {
        width: 15px;
        height: 50%;
        line-height: 50vh;
        border-left: 1px solid #ccc;
        cursor: pointer;
    }
    .footprint {
        border-top: 1px solid #ccc;
    }
}

.Layout {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(24, 1fr);

    .mt {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 16;
        border-bottom: 1px solid #ccc;
    }

    .mb {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 16;
        grid-row-end: 25;
    }

    .rt {
        grid-column-start: 3;
        grid-column-end: 4;
        grid-row-start: 1;
        grid-row-end: 25;
        border-left: 1px solid #ccc;

        display: flex;
        flex-direction: column;
        > div:first-child {
            padding: 5px 20px;
            border-top: 1px solid #ccc;
        }
        > div:last-child {
            flex: 1;
        }
    }

    .l {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 25;
        display: grid;
    }
    .lt {
        //     grid-column-start: 1;
        //     grid-column-end: 2;
        //     grid-row-start: 1;
        //     grid-row-end: 9;
        border-bottom: 1px solid #ccc;
        border-right: 1px solid #ccc;
        padding-bottom: 20px;
    }

    .lm {
        //     grid-column-start: 1;
        //     grid-column-end: 2;
        //     grid-row-start: 9;
        //     grid-row-end: 17;
        border-right: 1px solid #ccc;
        padding-bottom: 20px;
    }

    .lb {
        //     grid-column-start: 1;
        //     grid-column-end: 2;
        //     grid-row-start: 17;
        //     grid-row-end: 25;
        border-top: 1px solid #ccc;
        border-right: 1px solid #ccc;
    }
}
